:root {
  --base-color: #000;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.5rem;
}

header,
footer {
  padding: 4rem 0;
}

header {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  nav {
    a {
      margin-left: 1rem;
    }
  }
}

.container {
  padding: 0 2rem;
  margin: 0 auto;
  max-width: 1000px;
}
